
#app {
  background-image: url(static/img/what-the-hex.6633a16.png);
  height: 100vh;
  margin: 0px;  
  padding-top: 10px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
   margin:auto;
}
.wrap{
    padding-top: 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: auto;
    width: 400px;
}
.input{
    background-color: #fff;
    border: 0.1rem solid #d1d1d1;
    border-radius: .4rem;
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
    height: 3.8rem;
    padding: .6rem 1.0rem;
    width: 300px;
    height: 28px; 
    margin-right: 2px;
}
.submit{
    background-color: #9b4dca;
    border: 0.1rem solid #9b4dca;
    border-radius: .4rem;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-size: 1.1rem;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
}
.submit:hover{
    background-color: #606c76;
    border-color: #606c76;
    color: #fff;
    outline: 0;
}
.input:focus{
  border-color: #9b4dca;
  outline: 0;
}
.chat-content{
  /* background-color: #fff; */
  max-height: 600px;
  width: 400px;
  margin: auto;
  /* border-radius: 20px; */
  border: solid #9b4dca 0.1rem;
  overflow-y: auto;
  overflow-x: hidden;
}
.message {
    word-wrap: break-word;
    border-radius: 50px;
    margin: 5px 15px 10px;
    padding: 15px 20px;
    position: relative;
    font-weight: bold;
}
.message.to {
    text-align: right;
    background-color: #2095FE;
    color: #fff;
    margin-left: 100px;
    text-align: right;
}
.message.from {
  text-align: left;
    background-color: #E5E4E9;
    color: #363636;
    margin-right: 100px;
}
::-webkit-scrollbar-track
{
    border-radius: 8px;
    background-color: #F5F5F5;
}
::-webkit-scrollbar
{
    width: 10px;
    background-color: #F5F5F5;
}
::-webkit-scrollbar-thumb
{
    border-radius: 10px;
    background-color: lightgray;
}
